@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
html{font-size:10px;background:#f4f4f5}body{margin:0;font-family:'Space Mono', monospace;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}html,body,#root{width:100%;height:100%;margin:0;padding:0;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}*,*:before,*:after{box-sizing:border-box}

.mv{position:relative;width:100%;overflow:hidden;min-height:100vh}@supports (-webkit-touch-callout: none){.mv{min-height:-webkit-fill-available}}.logo{position:absolute;left:50%;top:calc(50% - 20px);margin-left:-60px;opacity:0;-webkit-animation:logoAnim 3s ease-out forwards;animation:logoAnim 3s ease-out forwards}.logo img{width:120px;height:auto}.canvas-container{z-index:-1;position:fixed;top:0;left:0;width:100%;height:100vh}.detail{position:absolute;width:100%;display:flex;flex-direction:column;align-items:flex-end;padding-right:48px;text-align:right;color:#333;z-index:1;top:65%}@media screen and (max-width: 768px){.detail{padding-right:5%}}.detail__text{margin:0;font-size:1.2rem;line-height:2;height:4em}.skill{position:absolute;left:0;top:20px;font-size:1.2rem;z-index:1;color:#333}.skill__list{margin:0;padding:0 0 0 20px}.skill__item{height:1em;line-height:1;margin-bottom:1em;padding-left:16px;list-style:none;position:relative}.skill__item:before{position:absolute;left:0;top:0;content:'-'}.company-info{padding:24px}.company-info table{width:100%;max-width:800px;margin:0 auto;border-collapse:collapse;background:rgba(255,255,255,0.2);border:1px solid #000}.company-info table tr:not(:first-child){border-top:1px solid #000}.company-info table th{white-space:nowrap}.company-info table th,.company-info table td{font-weight:normal;padding:1.5em 1em;text-align:left;font-size:1.4rem}@media screen and (max-width: 768px){.company-info table th,.company-info table td{font-size:1.2rem}}.company-info ul{padding:0;margin:0}.company-info ul li{padding:0 0 0 1em;margin:0;list-style:none;position:relative;font-size:1.4rem}.company-info ul li::before{content:"・";position:absolute;left:0}.company-info ul li+li{margin-top:8px}.scroll{position:absolute;bottom:20px;text-align:center;width:100%}.scroll span{font-size:1.2rem}.copyright{text-align:center;font-size:1.2rem;padding-bottom:40px}@media screen and (max-width: 768px){.copyright{padding-bottom:20px}}@-webkit-keyframes cursorAnim{from{visibility:visible;opacity:1}to{opacity:0}}@keyframes cursorAnim{from{visibility:visible;opacity:1}to{opacity:0}}@-webkit-keyframes logoAnim{from{opacity:0;-webkit-filter:blur(500px);filter:blur(500px)}to{opacity:1;-webkit-filter:blur(0);filter:blur(0)}}@keyframes logoAnim{from{opacity:0;-webkit-filter:blur(500px);filter:blur(500px)}to{opacity:1;-webkit-filter:blur(0);filter:blur(0)}}

